@import url('https://fonts.googleapis.com/css2?family=M+PLUS+1+Code:wght@200;300&display=swap');


*{
  margin: 0;
  padding: 0;
  font-family: 'M PLUS 1 Code', sans-serif;
  font-size: 16px;
  font-weight: 300;
}

body {
  background-color: #1a141b;
  display:flex;
  height: 100vh;
  align-items: center;
  justify-content: center;
}

p{
  align-self: flex-start;
  color:rgb(115, 78, 122);
  font-size: 20px;
  font-weight: 600;
  text-align: end;
  padding:0px 0px 0px 30px;
  margin-top: 10px;
}